#page-title {
    color: #CF6377;
}

.history-page {
    background-color: #FFD4CA;
    height: 90vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: #CF6377 #F7EDE2;
}

.history-list {
    padding: 1em;
    display: flex;
    width: 100vw;
    justify-content: space-evenly;
    flex-flow: row wrap;
}

.history-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20vw;
    padding: 1em;
    margin: 1em;
    border: #CF6377 1px solid;
    border-radius: 1em;
}

.history-item:hover {
    box-shadow: 0px 0px 40px 20px #CF6377;
    transform: scale(1.05, 1.05);
}

.history-image {
    transform: scale(0.75, 0.75);
}

.history-links {
    text-decoration: none;
    color: #CF6377;
}

.anime-title {
    color: #CF6377;
}

.clear-button {
    margin: 1em;
    background-color: #C33C54;
    border: 1px #C33C54 solid;
    font-size: medium;
    color: white;
    padding: 1em 2em;
    border-radius: 5px;
}

.clear-button:hover {
    background-color: gray;
    border: 1px black solid;
    cursor: pointer;
}